.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.body {
  margin-top: 20px;
  margin-left: 30px;
  margin-right: 30px;
  padding-top: 25px;
}

.footer {
  border-top-style: solid;
  border-width: 7px;
  border-top-color: #ae0007;

  margin-left: 30px;
  margin-right: 30px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 575px) {
  .body {
    margin-top: 90px;
    margin-left: 0px;
    margin-right: 0px;
  }

  .footer {
    margin-left: 10px;
    margin-right: 10px;
  }
}